/* Large devices (large desktops, 1200px and up) */
@media (max-width: $large-devices) {
	/*------------------------------------------------------------------
		[ HEADER ]
	-------------------------------------------------------------------*/
    #top-header .navbar .navbar-collapse #menu-top-menu > li > a{
        padding: 0 13px;
    }
    #top-header .top_nav .navbar-brand {
      padding: 25px 0 20px;
    }
    .advisor_my_dashboard .set_block .set_body .set_item .set_day{
        font-size: 12px;
    }
    .advisor_my_dashboard .set_block .set_body .set_item .day_available_times .time_trash{
        top: 25px !important;
        right: -10px;
    }
    .advisor_my_dashboard .set_block .set_body{
        font-size: 12px;
        .set_item .day_available_times{
            .start_time_select .time_text{
                font-size: 12px;
            }
            .end_time_select .time_text{
                font-size: 12px;
            }
        }
    }
    
	/*------------------------------------------------------------------
		[ CONTENT ]
	-------------------------------------------------------------------*/
	// code here...

	/*------------------------------------------------------------------
		[ FOOTER ]
	-------------------------------------------------------------------*/
	// code here...
}