/*------------------------------------------------------------------
	[  Login Form ]
-------------------------------------------------------------------*/
.login-page{
  body{
    background-color: #fff;
  }
}
.login-box{
  border: solid 1px #e6eaee;
  border-radius: 4px;

  &.admin_signin{

    .login-box-header{
      h1{
        font-family: $font-roboto;
        text-transform: none;
      }
    }
    .login-box-body{
      padding: 20px 30px;
      form{
        .form-field{
          .field-label{
            font-family: $font-roboto;
          }
          .field-input{
            margin-bottom: 15px;
            input{
              color: #000000;
              font-weight: normal !important;
            }
          }
          .btn{
            margin-bottom: 0;
          }
        }
      }
    }
  }

  &.registration_box{
    .login-box-header{
      h1{
        font-family: $font-roboto;
      }
    }
    .login-box-body{
      form{
        .form-field{
          .field-label{
            font-family: $font-roboto;
          }
          .btn{
            margin-bottom: 0;
          }
        }
      }
    }
  }

  &.settings-box{
    .login-box-header{
      background-color: #285187;
      border-radius: 4px 4px 0 0;
      h1{
        font-family: $font-roboto;
        font-size: 18px;
        text-align: left;
        color: #ffffff;
        padding: 20px 20px 20px 60px;
      }
    }
    .login-box-body{
      padding: 25px 60px;
      .form-field{
        .field-label{
          font-family: $font-roboto;
          font-size: 14px;
          text-align: left;
          color: #878787;
        }
        .field-input{
          margin-bottom: 25px;
        }
        .form-note{
          border-radius: 4px;
          background-color: #f8fafc;
          border: solid 1px #dfe3e9;
        }
      }
    }
  }

  &.process-box {
    .login-box-header {
      background-color: #285187;
      border-radius: 4px 4px 0 0;
      border-bottom: 0;
      .header_icon{
        .fa-envelope-o{
          object-fit: contain;
          color: #ffffff;
          margin-top: 20px;
          width: 32px;
          font-size: 32px;
        }
      }
      h1 {
        font-family: $font-roboto;
        font-size: 14px;
        text-align: center;
        color: #ffffff;
        padding: 15px 40px;
        display: inline-block;
        border-bottom: 2px solid;
      }
    }
    .login-box-body {
      background-color: #222c3c;
      h2 {
        font-family: $font-roboto;
        font-size: 21px;
        text-align: center;
        color: #ffffff;
        margin: 23px 0 27px 0;
        sub{
          font-size: 12px;
          color: #878787;
          margin-left: 15px;
        }
      }
      .form-field {
        .field-label {
          font-family: $font-roboto;
          font-size: 12px;
          font-weight: normal;
          line-height: 1.36;
          letter-spacing: 0.5px;
          text-align: left;
          color: #ffffff;
          text-transform: uppercase;
        }
        .field-input{
          .green-dot{
            display: none;
          }
          &.valid{
            position: relative;
            input{
              padding: 9px 30px 9px 13px;
            }
            .green-dot{
              color: #a6e50f;
              font-size: 7px;
              position: absolute;
              right: 16px;
              top: 14px;
              display: block;
            }
          }
          &.cvc{
            width: 60px;
          }
        }
      }
      .field-input{
        &.valid{
          position: relative;
          input{
            padding: 9px 30px 9px 13px;
          }
          .green-dot{
            color: #a6e50f;
            font-size: 7px;
            position: absolute;
            right: 16px;
            top: 14px;
            display: block;
          }
        }
        &.cvc{
          width: 60px;
        }
        .green-dot{
          display: none;
        }
        &.value{
          font-family: $font-roboto;
          font-size: 14px;
          letter-spacing: 0.5px;
          text-align: left;
          color: #ffffff;
        }
      }
      .btn-blue{
        letter-spacing: 0.5px;
        text-align: center;
        text-transform: uppercase;
        font-weight: normal;
        margin-bottom: 0;
        margin-top: 50px;
      }
    }
    .process-box-footer{
      height: 70px;
      background-color: #273142;
    }
  }

  .login-box-header{
    background-color: #f8fafc;
    border-bottom: solid 1px #e6eaee;
    text-align: center;

    h1{
      text-align: center;
      color: #878787;
      padding: 20px 15px;
      margin: 0;
      font-size: 24px;
      text-transform: none;
    }
  }

  .form-field{
    .field-label{
      font-size: 14px;
      font-weight: normal;
      line-height: 1.36;
      text-align: left;
      color: #878787;
      text-transform: inherit;
    }

    .field-input{
      input{
        border-radius: 4px;
        background-color: #f8fafc !important;
        border: solid 1px #dfe3e9 !important;
        font-size: 14px !important;
        line-height: 1.36;
        text-align: left;
        color: #354052 !important;
        padding: 11px 20px;
      }

    }
    .btn{
      width: 100% !important;
      font-size: 14px;
      text-transform: inherit;
    }
  }

  .login-box-body{
     padding: 18px 30px;

  }

  .login-box-footer {
    border-top: solid 1px #e6eaee;
    text-align: center;
    padding: 30px 30px;
    position: relative;

    .forgot-password{
      font-family: $font-sansPro;
      font-size: 14px;
      line-height: 1.36;
      color: #7f8fa4;
      margin: 0 auto;
