    }
  }
  .form-note{
    border-radius: 4px;
    background-color: #f8fafc;
    border: solid 1px #dfe3e9;
    padding: 12px 15px;
    text-align: center;
    margin-bottom: 25px;
    color: #878787;
    font-family: $font-roboto;
    font-size: 14px;
  }
}

.footer-links{
    a{
