/********
 * Price Table Section
 ******/
#price_table_section{
  position: relative;

  .payment-banner{
    background: rgba(40, 81, 135, 0.8);
    background-image: url("../images/rectangle-3.png");
    background-size: cover;
    min-height: 339px;
    font-family: $font-roboto;
    text-align: right;
    color: #ffffff;

    ul{
      list-style: none;
      padding: 28px;
      li{
        display: inline;
        margin-left: 6px;

        &.active, &:hover{
          a{
            color: #ffffff;
            border-bottom: 2px solid #fff;
          }
        }
        a{
          font-family: $font-roboto;
          font-size: 14px;
          font-weight: bold;
          letter-spacing: 0.5px;
          text-align: left;
          color: #878787;
          padding: 2px 4px;
          display: inline-block;
        }
      }
    }
  }

  .package_table{
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    top:85px;

    .nav-tabs{
      margin: 0 auto;
      text-align: center;
      max-width: 570px;
      border: 0;
      border-radius: 0;

      li{
        border: 0;
        border-radius: 0;
        text-align: center;

        &:first-child{
          a, a.active, a:hover{
            border-radius: 25px 0 0 25px !important;
            padding-left: 40px;
            padding-right: 40px;
          }
        }
        &:last-child{
          a, a.active, a:hover{
            border-radius: 0 25px 25px 0 !important;
            padding-left: 40px;
            padding-right: 40px;
          }
        }

        a{
          font-family: $font-roboto;
          font-size: 18px;
          font-weight: bold;
          letter-spacing: 0.6px;
          text-align: left;
          color: #285187;
          background-color: #ffffff;
          text-transform: uppercase;
          border: 0;
          border-radius: 0;
          padding: 13px 20px;
          display: inline-block;

          &.active, &:hover{
            color: #ffff;
            background-color: #285187;
            border: 0;
            border-radius: 0;
            padding: 13px 20px;
          }
        }
      }
    }

    &_content{
      margin-top: 24px;
      .container{
        background: #fff;
      }

      .price_list{
        border: 1px solid #f0f1f2;

        &_item{
            position: relative;

          &.special_item{
            .price_list_item_title{
              color: #285187;
            }
            .price_list_item_price{
              color: #285187;
            }
          }
          &.special_item_active{
            background-color: #f3f5f5;
          }

          &_inner{
            padding: 28px 24px 80px 24px;
          }
          & + .price_list_item{
            border-left: 1px solid #f0f1f2;
          }
          &_title{
            font-family: $font-roboto;
            font-size: 24px;
            text-align: center;
            color: #000000;
          }
          &_price{
            font-family: $font-roboto;
            font-size: 58px;
            text-align: center;
            color: #323c47;
            sup{
              color: #878787;
              font-size: 18px;
              top: -1.5em;
              left: -10px;
            }
          }
          &_features{
            font-family: $font-roboto;
            font-size: 14px;
            line-height: 2.36;
            text-align: left;
            color: #989898;
            margin-bottom: 20px;

            i{
              color: #a6e50f;
              font-weight: bold;
            }
          }
          &_btn{
            position: absolute;
            width: auto;
            left: 0;
            right: 0;
            padding: 0 42px;
            bottom: 13px;
            a{
              font-size: 14px;
              display: block;
            }
          }
        }
      }
      .price_detail_table{
        margin-top: 36px;
        margin-bottom: 113px;

        table{
          display: inline-table;

          thead{
            width: 100%;
            .plan-title{
              font-size: 24px;
            }
          }
          tbody{
            width: 100%;
            .monthly-price{
              font-size: 58px;
            }
          }
        }
      }
    }
  }
}

