/*admin_deals_dashboard*/
.admin_deals_dashboard{
  .cyx-breadcrumb{
    margin-top: 45px;
    margin-bottom: 30px;
  }

  .company_headline{
    .company_title{
      font-size: 36px;
      line-height: 1.67;
      color: #878787;
      margin-bottom: 25px;
      }
    }

  .table_main_head{
    border-radius: 4px 4px 0 0;
    background-color: #ffffff;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);
    border: 1px solid #e6eaee;
    .btn_add_company{
      border-right: 2px solid #f5f5f5;
      text-align: center;
      .btn{
        margin: 20px 0;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 900;
        letter-spacing: 1.5px;
      }
      .btn-blue{
        background-color: #1875f0;
        background-image: linear-gradient(to bottom, #1875f0, #1875f0 98%);
        border: 1px solid #1875f0;
      }
    }
    .select_del_edit{
      border-right: 2px solid #f5f5f5;
      .check_drop{
        .field-section{
          display: inline-block;
          .checkbox{
            padding-bottom: 10px;

            [type="checkbox"]:not(:checked),
            [type="checkbox"]:checked {
              position: absolute;
              left: -9999px;
              outline: none;
            }
            [type="checkbox"]:not(:checked) + label,
            [type="checkbox"]:checked + label {
              position: relative;
              padding-left: 28px;
              cursor: pointer;
              color: #212121;
              padding-top: 3px;
              outline: none;
            }

             /* checkbox aspect */
            [type="checkbox"]:not(:checked) + label:before {
              content: '';
              position: absolute;
              left:0;
              top: 4px;
              border: 1px solid #bbb;
              border-radius: 4px;
              background: #fff;
              box-shadow: inset 0 1px 3px rgba(0,0,0,.1);
              outline: none;
              width: 20px;
              height: 20px;
            }
            [type="checkbox"]:checked + label:before {
              content: '';
              position: absolute;
              left:0;
              top: 4px;
              width: 22px;
              height: 22px;
              border: 1px solid #bbb;
              border-radius: 4px;
              background: #1875f0;
              box-shadow: inset 0 1px 3px rgba(0,0,0,.1);
              outline: none;
            }
            /* checked mark aspect */
            [type="checkbox"]:not(:checked) + label:after,
            [type="checkbox"]:checked + label:after {
              content: '\f00c';
              position: absolute;
              line-height: 0.8;
              transition: all .2s;
              outline: none!important;
              top: 10px;
              left: 4px;
              font-size: 14px;
              font-family: FontAwesome;
              color: #ffffff;
            }
            /* checked mark aspect changes */
            [type="checkbox"]:not(:checked) + label:after {
              opacity: 0;
              transform: scale(0);
            }
