
/* Small devices (tablets, 767px and up) */
@media (max-width: 930px) {
  .header_notification .header_notification_icons{
    float: none;
    .icons a{
      padding: 5px;
    }
  }
}

@media (max-width: $small-devices) {
  #navbarNavAltMarkup{
    &.collapse{
      display: none;

      &.show{
        display: block;
      }
    }
    ul{
      li{
        display: block;
      }
    }
  }
  .navbar{
    .navbar-brand{
      display: inline-block !important;
      margin: 0;
    }
    button{
      display: inline-block !important;
      text-align: right;
      float: right;
      margin-top: 24px;
    }
  }

  #profile-menu{
    margin-top: 24px;
  }
  .advisor_my_dashboard .set_block .set_body{
    font-size: 14px;
    .set_item .set_day{
      font-size: 14px;
    }
    .set_item .day_available_times{
      .start_time_select .time_text{
        font-size: 14px;
      }
      .end_time_select .time_text{
          font-size: 14px;
      }
      .time_trash{
        top: 25px !important;
        right: -4px;
      }
    }
  }
}

