/* MEDIA QUERIES SCREEN SIZES */
$xsmall-devices		: 480px;
$small-devices		: 767px;
$medium-devices		: 991px;
$large-devices		: 1199px;
$xlarge-devices		: 1280px;

/* URL's */
$img-url			: '../images/';

/* Fonts / Sizes */
$font-default		      : 'Lato', sans-serif;
$font-sansPro             : 'Source Sans Pro', sans-serif;
$font-proxinova-light     : 'ProximaNova-Light';
$font-proxinova-bold      : 'ProximaNova-Bold';
$font-proxinova-semibold  : 'ProximaNova-Semibold';
$font-proxinova-black     : 'ProximaNova-Black';
$font-roboto              : 'Roboto';


/* Primary COLORS */
$colo-default		: #21416b;
$dark-gray			: #fdfdfd;
$blue               : #285187;
$powder-blue        : #b9ddfb;
$orange		        : #ff5607;
$green              : #39b54a;
$gray               : #878787;
$white              : #ffffff;

/* Status Message COLORS */
$error-color		: #ed171f;
$success-color      : #169207;


$enable-flex: true;

/* Global Styles */
html{
	overflow-y: scroll;
}
body{
  background-color: #ffffff;
  font-family: $font-roboto !important;
}
*{
    outline: none !important;
}
.container .row {
  margin-left: -15px !important;
  margin-right: -15px !important;
}

a{
    text-decoration: none !important;
}
.clearBoth{
    clear: both;
    float: none;
}
h1{
    color: $colo-default ;
    font-family: $font-roboto;
    font-size: 32px;
    margin-bottom: 10px;
    margin-top: 36px;
    text-transform: capitalize;
}
h2{
    color: $colo-default ;
    font-family: $font-roboto;
    font-size: 28px;
    margin-bottom: 20px;
}
h3{
    color: $colo-default ;
    font-family: $font-roboto;
    font-size: 24px;
    margin-bottom: 20px;
}
h4{
    color: $colo-default ;
    font-family: $font-roboto;
    font-size: 20px;
    margin-bottom: 20px;
}
h5{
    color: $colo-default ;
    font-family: $font-roboto;
    font-size: 18px;
    margin-bottom: 20px;
}
h6{
    color: $colo-default ;
    font-family: $font-roboto;
    font-size: 16px;
    margin-bottom: 20px;
}

.page__content{

    >p, >h2, >h3, >h4, >h5, >h6{
        margin-top: 0;
    }
    p{
        color: $colo-default;
        font-family: $font-default;
        font-size: 12px;
        line-height: 20px;
        margin-bottom: 19px;

        a{
            color: $blue;
        }
    }
    strong{
        color: $dark-gray;
    }
    .post__featured{
        margin-bottom: 0;
    }

    /* Featured Image*/

    img{
        float: left;
        margin-bottom: 20px;
        margin-right: 30px;
        margin-top: 4px;
        max-width: 100%;
        height: auto;
    }

    /* Order/ UnOrder list*/
    >ul{
        margin-left: 95px;
        margin-bottom: 20px;

    }

    ul{
        overflow: hidden;
        list-style: none;
        >li{
            color: $colo-default;
            font-family: $font-default;
            font-size: 12px;
            margin-top: 14px;
            margin-bottom: 0;

            &:before{
                color: $dark-gray;
                content: "\f101";
                font-family: FontAwesome;
                margin-left: -38px;
                margin-right: 5px;
            }

            a{
                color: $blue;
                text-decoration: none;
            }

            &:first-child{
                margin-top: 0;
            }
        }


    }

    ol{
        @extend ul;
        list-style: outside decimal;

        >li{
            &:before{
                content:"";
                margin: 0;
            }
        }
        ol{
            list-style: outside lower-alpha;
            padding-left: 20px;

        }
    }

    .attachment-contact-thumb{
        float: none;
        margin-right: 0;
    }

}

.btn{
	margin-bottom: 14px;
    font-family: $font-roboto;
    //font-weight: 600;
    color: #fff !important;
    border: 1px solid $blue;
    padding: 13px 30px;
    border-radius: 4px;
    text-transform: capitalize;
    text-align: center;

    &:hover{
      background-color: $blue;
      color: #fff !important;
    }

  &.btn-blue{
    background-color: $blue;
    background-image: linear-gradient(to bottom, #285187, #285187 98%);
    border: 1px solid #285187;
    color: #fff !important;

    &:hover{
      background-color: #fff;
      color: #ffffff !important;
    }
    &:focus{
      background-color: $blue;
      background-image: linear-gradient(to bottom, #285187, #285187 98%);
      border: 1px solid #285187;
      color: #ffffff !important;
      box-shadow: none;
    }
  }

  &.btn-dodgerblue{
    background-color: #1875f0;
    background-image: linear-gradient(to bottom, #1875f0, #1875f0 98%);
    border: 1px solid #1875f0;
    color: #fff !important;

    &:hover{
      background-color: #1875f0;
      color: #ffffff !important;
    }
    &:focus{
      background-color: #1875f0;
      background-image: linear-gradient(to bottom, #1875f0, #1875f0 98%);
      border: 1px solid #1875f0;
      color: #ffffff !important;
      box-shadow: none;
    }
  }

  &.btn-white{
    background-color: $white;
    background-image: linear-gradient(to bottom, #ffffff, #ffffff 98%);
    border: 2px solid #878787;
    color: #ffffff !important;

    &:hover{
      background-color: #ffffff;
    }
    &:focus{
      background-color: $white;
      background-image: linear-gradient(to bottom, #ffffff, #ffffff 98%);
      border: 2px solid #878787;
      color: #ffffff !important;
      box-shadow: none;
    }
  }

  &.btn-green{
    background-color: $green;
    background-image: linear-gradient(to bottom, #39b54a, #34aa44 98%);
    border: 1px solid #249533;
    color: #ffffff !important;

    &:hover{
      background-color: #249533;
      color: #fff !important;
    }
    &:focus{
      background-color: $green;
      background-image: linear-gradient(to bottom, #39b54a, #34aa44 98%);
      border: 1px solid #249533;
      color: #ffffff !important;
      box-shadow: none;
    }
  }

  &.btn-gray{
    background-color: $gray;
    background-image: linear-gradient(to bottom, #878787, #878787 98%);
    border: 1px solid #878787;
    color: #ffffff !important;

    &:hover{
      color: #fff !important;
    }
    &:focus{
      background-color: $gray;
      background-image: linear-gradient(to bottom, #878787, #878787 98%);
      border: 1px solid #878787;
      color: #ffffff !important;
      box-shadow: none;
    }
  }

  &.btn-orange{
    background-color: $orange;
    color: #fff !important;
    border: 1px solid $orange;

    &:hover{
      background-color: #fff;
      color: $orange !important;
    }
  }
  &.btn-red{
    background-color: #d0011b;
    color: #fff !important;
    border: 1px solid #d0011b;

    &:hover{
      background-color: #fff;
      color: #d0011b !important;
    }
  }
  &.btn-active{
    background-color: #f3f5f5;
    color: #39b54a !important;
    border: 1px solid #39b54a;
    border-radius: 8px;
    font-size: 18px !important;
    letter-spacing: -0.2px;
  }
}

.btn + .btn{
	margin-left: 15px;
}

button + button {
    margin-left: 15px;
}


