/*Pagination Container*/
.pagination_container{
  margin-top: 20px;
  margin-bottom: 20px;
  .spacer{
    text-align: center;
    .page_display{
      font-size: 14px;
      color: #878787;
    }
  }
}

.pagination{
  text-align: center;
  display: table;
  margin-top: 15px !important;
  margin: 0 auto;

  .page-numbers{
    color: #0275d8 ;
    background: #fff;
    border: 1px solid #e6e6e6 ;
    display: table-cell;
    font-size: 16px;
    text-align: center;
    text-decoration: none;
    border-right: 1px solid #eceeef ;
    border-top: 1px solid #eceeef ;
    border-bottom: 1px solid #eceeef ;
    border-left: 1px solid #eceeef;
    cursor: pointer;

    &.prev{
       margin-right: 32px;
    }
    &.next{
       margin-left: 32px;
    }
    a{
      color: #0275d8;
      padding: 6px 16px;
    }
     span{
       color: #ffffff;
       padding: 6px 16px;
     }

    &.current, &:hover, &.active{
       background-color: #0275d8 ;
       color: #ffffff ;
       padding: 6px 0;
       border-color: #0275d8 ;
       a{
         color: #fff;
         padding: 6px 16px;
       }
    }
  }
}

