//Switch Button
.switch {
  position: relative;
  display: inline-block;
  width: 64px;
  height: 26px;

  &:before {
    content: "";
    width: 10px;
    top: 6px;
    left: 10px;
    position: relative;
    font-size: 13px;
    color: #fff;
    z-index: 9;
  }
  &:after {
    content: "";
    width: 10px;
    top: 6px;
    left: 25px;
    position: relative;
    font-size: 13px;
    color: #fff;
  }

  input {
    display:none;

    &:checked + .slider {
      background-color: #a6e50f;
    }
    &:focus + .slider {
      box-shadow: 0 0 1px #a6e50f;
    }
    &:checked + .slider:before {
      -webkit-transform: translateX(40px);
      -ms-transform: translateX(40px);
      transform: translateX(40px);
    }
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #939ea7;
    -webkit-transition: .4s;
    transition: .4s;

    &:before {
      position: absolute;
      content: "";
      height: 26px;
      width: 26px;
      left: 8px;
      bottom: 4px;
      background-color: white;
      -webkit-transition: .4s;
      transition: .4s;
    }
    &.round {
      border-radius: 34px;

      &:before {
        border-radius: 50%;
      }
    }
  }
}

/*--------------------------
   [ Default Form ]
----------------------------*/

form{
  .field-section{
    + .field-section{
      margin-top: 60px;
    }
    .field-section-title{
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0;
      text-align: left;
      color: #000000;
      margin-top: 45px;
      margin-bottom: 40px;
      padding-left: 7px;
    }
  }
  .form-field{
      .field-label{
          font-family: $font-roboto;
          font-size: 12px;
          font-weight: normal;
          color: #878787;
          margin-bottom: 10px;
      }
      .field-input{
          margin-bottom: 11px;

          //Input Box
          input[type=text], input[type=number], input[type=email], input[type=password], input[type=url]{
            background-color: #ffffff;
            border: 1px solid #878787;
            border-radius: 4px;

            font-family: $font-roboto;
            font-size: 12px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: 0;
            text-align: left;
            color: #878787;
            padding: 9px 13px;
            width: 100%;
          }

          //Select Box
          select{
            background-color: #ffffff;
            border: solid 1px #ced0da;
            border-radius: 4px;
            background-image: linear-gradient(to top, #f2f4f7, #ffffff);
            font-family: $font-roboto;
            font-size: 14px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: 0;
            text-align: left;
            color: #000;
            padding: 9px 20px;
            width: 100%;
            text-transform: capitalize;
          }
          //Text Area
          textarea{
            background-color: #ffffff;
            border: solid 1px #979797;
            font-family: $font-roboto;
            font-size: 14px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: 0;
            text-align: left;
            color: #000;
            padding: 17px 20px;
            width: 100%;
            resize: vertical;
          }
          //Radio Button
            [type="radio"]:not(:checked),
            [type="radio"]:checked {
              position: absolute;
              left: -9999px;
              outline: none;
            }
            [type="radio"]:not(:checked) + label,
            [type="radio"]:checked + label {
              position: relative;
              padding-left: 22px;
              padding-right: 22px;
              cursor: pointer;
              padding-top: 3px;
              outline: none;
              font-size: 12px;
              color: #878787;
              display: inline-block;
            }

            /* checkbox aspect */
            [type="radio"]:not(:checked) + label:after{
              content: '\f10c';
              outline: none;
              font-family: FontAwesome;
              font-size: 18px;
              position: absolute;
              left: 0;
              top: 0;
            }

            /* checked mark aspect */

            [type="radio"]:checked + label:after {
              content: '\f192';
              transition: all .2s;
              outline: none;
              font-size: 18px;
              font-family: FontAwesome;
              position: absolute;
              left: 0;
              top: 0;
            }
            /* checked mark aspect changes */
            [type="radio"]:not(:checked) + label:after {
              opacity: 1;
              transform: scale(1);
            }
            [type="radio"]:checked + label:after {
              opacity: 1;
              transform: scale(1);
            }
            /* disabled checkbox */
            [type="radio"]:disabled:not(:checked) + label:before,
            [type="radio"]:disabled:checked + label:before {
              box-shadow: none;
              border-color: #bbb;
              background-color: #ddd;
              outline: none;
            }
            [type="radio"]:disabled:checked + label:after {
              color: #999;
              outline: none;
            }
            [type="radio"]:disabled + label {
              color: #aaa;
              outline: none;
            }
            /* accessibility */
            [type="radio"]:checked:focus + label:before,
            [type="radio"]:not(:checked):focus + label:before {
              border: 1px dotted $blue;
            }
            [type="radio"] + label {
              display: block;
            }

          /////Checkbox
          [type="checkbox"]:not(:checked),
          [type="checkbox"]:checked {
            position: absolute;
            left: -9999px;
            outline: none;
          }
          [type="checkbox"]:not(:checked) + label,
          [type="checkbox"]:checked + label {
            position: relative;
            padding-left: 28px;
            cursor: pointer;
            color: #212121;
