          }content: ':heavy_check_mark:';
          /* checked mark aspect */
          [type="checkbox"]:not(:checked) + label:after,
          [type="checkbox"]:checked + label:after {
            content: '\f00c';
            position: absolute;
            line-height: 0.8;
            color: $blue;
            transition: all .2s;
            outline: none;
            top: 10px;
            left: 4px;
            font-size: 14px;
            outline: none;
            font-family: FontAwesome;
          }
          /* checked mark aspect changes */
          [type="checkbox"]:not(:checked) + label:after {
            opacity: 0;
            transform: scale(0);
          }
          [type="checkbox"]:checked + label:after {
            opacity: 1;
            transform: scale(1);
          }
          /* disabled checkbox */
          [type="checkbox"]:disabled:not(:checked) + label:before,
          [type="checkbox"]:disabled:checked + label:before {
            box-shadow: none;
            border-color: #bbb;
            background-color: #ddd;
            outline: none;
          }
