/*Popover*/
.popover_wrapper {
  position: relative;
  display: block;

  &.popover_wrapper:hover .popover{
    z-index: 10;
    opacity: 1;
    visibility: visible;
    transform: translate(0,-20px);
    transition: all 0.5s cubic-bezier(0.75, -0.02, 0.2, 0.97);
  }
  a{
    i{
      color: #d8d8d8;
    }
  }
  .popover{
    opacity: 0;
    visibility: hidden;
    border: 1px solid #ebebeb;
    border-radius: 4px;
    display: block;
    width: 100px;
    top: 50px;
    left: -55px;

    &.popover::before{
      content: "";
      border-width: 11px;
      position: absolute;
      z-index: -1;
      display: block;
      top: 50px;
      width: 0;
      height: 0;
      border-color: transparent;
      border-style: solid;
      transition-duration: 0.3s;
      transition-property: transform;
    }
    &.popover::after {
      content: "";
      border-width: 10px;
      position: absolute;
      display: block;
      width: 0;
      height: 0;
      border-color: transparent;
      border-style: solid;
    }
    &.popover-bottom::before{
      top: -11px;
      margin-left: -11px;
      border-bottom-color: rgba(0,0,0,.15);
      left: 62%;
      border-top-width: 0;
    }
    &.popover-bottom::after{
      top: -10px;
      margin-left: -10px;
      border-bottom-color: #ffffff;
      left: 62%;
      border-top-width: 0;
    }
    .popover-title{
      color: #878787;
      font-family: $font-roboto;
      text-align: left;
      padding: 8px 14px;
      margin-top: 0;
      margin-bottom: 0;
      font-size: 18px;
      background-color: #f7f7f7;
      border-bottom: 1px solid #ebebeb;
      border-top-right-radius: calc(.3rem - 1px);
      border-top-left-radius: calc(.3rem - 1px);

      &.popover-title::before{
        position: absolute;
        top: 0;
        left: 50%;
        display: block;
        width: 20px;
        margin-left: -10px;
        content: "";
        border-bottom: 1px solid #f7f7f7;
      }
    }
    .popover-content{
      font-size: 12px;
      ul{
        list-style: none;
        margin: 0;
        padding-left: 0;
        li{
          a{
            color: #878787;
            font-size: 12px;
            display: block;
            padding: 5px 15px;
            i{
              color: $blue !important;
              padding-right: 10px !important;
            }
          }
          &:hover{
            color: #ffffff;
            background-color: #f7f7f7;
          }
        }
      }
    }
  }
}

