/*Checkbox*/
.checkbox{
  padding-bottom: 10px;
  margin-bottom: 3px;
  position: relative;

  [type="checkbox"]:not(:checked),
  [type="checkbox"]:checked {
    position: absolute;
    left: -9999px;
    outline: none;
  }
  [type="checkbox"]:not(:checked) + label,
  [type="checkbox"]:checked + label {
    position: relative;
    padding-left: 28px;
    cursor: pointer;
    color: #212121;
    padding-top: 3px;
    outline: none;
  }

   /* checkbox aspect */
  [type="checkbox"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left:0;
    top: 4px;
    border: 1px solid #bbb;
    border-radius: 4px;
    background: #fff;
    box-shadow: inset 0 1px 3px rgba(0,0,0,.1);
    outline: none;
    width: 20px;
    height: 20px;

  }
  [type="checkbox"]:checked + label:before {
    content: '';
    position: absolute;
    left:0;
    top: 4px;
    width: 22px;
    height: 22px;
    border: 1px solid #bbb;
    border-radius: 4px;
    background: #285187;
    box-shadow: inset 0 1px 3px rgba(0,0,0,.1);
    outline: none;

  }
  /* checked mark aspect */
  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    content: '\f00c';
    position: absolute;
    line-height: 0.8;
    transition: all .2s;
    outline: none!important;
    top: 10px;
    left: 4px;
    font-size: 14px;
    font-family: FontAwesome;
    color: #ffffff;
  }
  /* checked mark aspect changes */
  [type="checkbox"]:not(:checked) + label:after {
    opacity: 0;
    transform: scale(0);
  }
  [type="checkbox"]:checked + label:after {
    opacity: 1;
    transform: scale(1);
  }
  /* disabled checkbox */
  [type="checkbox"]:disabled:not(:checked) + label:before,
  [type="checkbox"]:disabled:checked + label:before {
    box-shadow: none;
    border-color: #bbb;
    background-color: #ddd;
    outline: none;
  }
  [type="checkbox"]:disabled:checked + label:after {
    color: #999;
    outline: none;
  }
  [type="checkbox"]:disabled + label {
    color: #aaa;
    outline: none;
  }
  /* accessibility */
  [type="checkbox"]:checked:focus + label:before,
  [type="checkbox"]:not(:checked):focus + label:before {
    border: 1px dotted $blue;
  }
  [type="checkbox"] + label {
    display: block;
  }
}

