/* Medium devices (desktops, 991px and up) */
@media (max-width: 1154px){
    .header_notification .header_notification_icons .icons a{
        padding: 7px;
    }
}
@media (max-width: 1100px){
    .header_notification .header_notification_icons .icons a{
        padding: 6px;
    }
}
@media (max-width: 1045px){
    .header_select_box .form-field .field-input select{
        padding: 15px;
    }
    .header_notification .header_notification_icons .icons a{
        padding: 5px;
    }
}


