    /*************** 
     * Enable Responsive Menu 
     ***************************/
    .navbar .navbar-collapse #menu-top-menu .dropdown-toggle {
        display: block !important;
        padding: 0 30px;
    }
    
    .navbar {
        .navbar-header{ 
            display: block;
            float: none;
            margin-top: 0;
            
            .navbar-toggle {
                background: none;  
                border: none;
                display: block;
                margin: 0 auto;
                text-align: center;
                i{
                    color: #ffffff;
                }
                
            }
            
            //> a.navbar-brand{
            //    display: none;
            //}
        }
        .navbar-collapse{
                border: none !important;
                //display: none;
                position: relative;
                left: 0px;
                width: 100%;
                padding: 0px;
                

                //&.collapse{
                //    display: none !important;
                //}
                //
                //&.in{
                //    display: block !important;
                //}
                
                /*************** 
                 * Style Responsive Menu 
                 ***************************/
                #menu-top-menu{
                    float: none;
                    padding: 0px;
                    
                    >li {
                        > a{
                          color: #ffffff;  
                            display: block;
                        }
                        
                        &:hover, &.current-menu-item, &.current-menu-item{
                            >a{
                                color: #ffffff;
                            }
                        }
                    }
                    
                    li{
                        display: block;
                        float: none;
                        
                        span.dropdown-toggle{
                            &.arrow{
                                position: absolute !important;
                                right: 0;
                                top: 8px;
                            }
                            &:after {
                                display: block;
                            }
                        }
                        &:hover{
                            .dropdown-menu{
                                display: none;
                                background: none;
                            }
                        }
                        &.open{
                            >a{
                                background: none;
                            }
                            >.dropdown-menu{
                                display: block;
                            }
                        }
                        
                        .dropdown-menu{
                            position: relative !important;
                            width: 100%;
                            box-shadow: none;
                            background: none;
                            
                            li{ 
                                position: relative;
                                a{
                                    display: inline-block;  
                                    color: #fff !important;
                                    font-size: 18px;
                                    font-family: $font-default;

                                }
                                &:hover{
                                    > a{
                                       color: #000 !important; 
                                    }
                                }
                            }
                            
                            .dropdown-toggle{
                                &.arrow{
                                    position: absolute !important;
                                    right: 0;
                                    top: 8px;
                                }
                                &:after{
                                    color: #fff !important;
                                    line-height: normal;
                                    padding-top: 3px;
                                }
                            }
                            
                            .dropdown-menu{
                                left: 0px !important;
                                
                                a{
                                    padding: 4px 20px 4px 30px !important;
                                    
                                }
                                
                            }
                        }
                    }
                }
                
                
                
            }

    }
    
    .navbar .navbar-collapse #menu-top-menu > li.menu-item-has-children:hover::after{
        border: 0;
    }
	 .navbar .navbar-collapse #menu-top-menu > li > a{
	 	border-left: medium none;
        padding: 13px;
    }
    .header_select_box .form-field .field-input select{
        padding: 15px 10px;
    }
    .header_notification .header_notification_icons .icons a{
        padding: 4px;
    }
    .advisor_my_dashboard .info_container .info_block .info_body_rows dd{
        font-size: 13px;
    }
    .advisor_my_dashboard .set_block .set_body{
        font-size: 6px;
        .set_item{
            .set_day{
            font-size: 10px;
            }
            .day_available_times .start_time_select .time_text{
                font-size: 7px;
            }
            .day_available_times .end_time_select .time_text{
                font-size: 7px;
            }
        }
    }
